import React, {Component} from 'react';
import DiscreteSlider from "./slider";
import SimpleSelect from "./simpleSelect";

class Menu extends Component {
    render() {
        return (
            <nav className="nav alert-dark">
                <button className="btn btn-primary btn-lg m-2" onClick={this.props.onRefresh}>Refresh</button>
                <SimpleSelect/>
                <DiscreteSlider
                    onChange={this.props.onChangeSpeed}
                    title="speed"
                    marks={true}
                    default={10}
                    step={10}
                    min={10}
                    max={50}
                />
                <DiscreteSlider
                    onChange={this.props.onChangeValues}
                    title="Total Number"
                    marks={false}
                    default={100}
                    step={1}
                    min={10}
                    max={500}
                />
                <button className="btn btn-warning btn-lg m-2" onClick={this.props.onVisualize}>Visulize</button>


            </nav>
        );
    }
}

export default Menu;